import React from 'react'
import{Container, Row, Col} from "react-bootstrap"
const HeroComp = () => {
  return (
    <div className='hero min-vh-100 w-100' id='home'>
      <Container>
        <Row>
            <Col className='text-white text-center'>
                <h1 className='animate__animated animate__fadeInUp'>Adam Fradana</h1>
                <p className='text-white-50 animate__animated animate__fadeInUp animate__delay-1s'>Front end Website</p>
            </Col>
        </Row>
      </Container>
    </div>
  )
}

export default HeroComp
